import React from "react";
import { createComponent } from "webiny-app";
import styles from "./styles/LayoutWidgetFunctions.scss";

class WidgetActions extends React.Component {
    render() {
        const {
            children,
            modules: { Icon }
        } = this.props;

        return (
            <div className={styles.widgetFunctions}>
                <a href="javascript:void(0)" className="editor-row-functions">
                    <Icon icon={["fas", "pencil-alt"]} />
                </a>
                <div className={styles.functions}>
                    <ul>
                        {React.Children.map(children, (child, i) => (
                            <li key={i}>{child}</li>
                        ))}
                    </ul>
                </div>
            </div>
        );
    }
}

export default createComponent(WidgetActions, { modules: ["Icon"] });
